html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  min-height: 100vh;
  background: linear-gradient(135deg, #7c1599 0%, #921099 48%, #7e4ae8 100%) 0 0 / cover;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.player {
  max-width: 750px;
  border: 5px solid rgba(0, 0, 0, .2);
  font-size: 0;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(0, 0, 0, .2);
}

.player:-webkit-full-screen {
  max-width: none;
  width: 100%;
}

.player:-ms-fullscreen {
  max-width: none;
  width: 100%;
}

.player:fullscreen {
  max-width: none;
  width: 100%;
}

.player__video {
  width: 100%;
}

.player__button {
  color: #fff;
  text-align: center;
  cursor: pointer;
  max-width: 50px;
  background: none;
  border: 0;
  outline: 0;
  padding: 0;
  line-height: 1;
}

.player__button:focus {
  border-color: #ffc600;
}

.player__slider {
  width: 10px;
  height: 30px;
}

.player__controls {
  width: 100%;
  background: rgba(0, 0, 0, .1);
  flex-wrap: wrap;
  transition: all .3s;
  display: flex;
  position: absolute;
  bottom: 0;
  transform: translateY(100%)translateY(-5px);
}

.player:hover .player__controls {
  transform: translateY(0);
}

.player:hover .progress {
  height: 15px;
}

.player__controls > * {
  flex: 1;
}

.progress {
  height: 5px;
  cursor: ew-resize;
  background: rgba(0, 0, 0, .5);
  flex: 10 100%;
  transition: height .3s;
  display: flex;
  position: relative;
}

.progress__filled {
  width: 50%;
  background: #ffc600;
  flex: 0 50%;
}

input[type="range"] {
  -webkit-appearance: none;
  width: 100%;
  background: none;
  margin: 0 5px;
}

input[type="range"]:focus {
  outline: none;
}

input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  background: rgba(255, 255, 255, .8);
  border: .2px solid rgba(1, 1, 1, 0);
  border-radius: 1.3px;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0), 0 0 1px rgba(13, 13, 13, 0);
}

input[type="range"]::-webkit-slider-thumb {
  height: 15px;
  width: 15px;
  cursor: pointer;
  -webkit-appearance: none;
  background: #ffc600;
  border-radius: 50px;
  margin-top: -3.5px;
  box-shadow: 0 0 2px rgba(0, 0, 0, .2);
}

input[type="range"]:focus::-webkit-slider-runnable-track {
  background: #bada55;
}

input[type="range"]::-moz-range-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  background: #fff;
  border: .2px solid rgba(1, 1, 1, 0);
  border-radius: 1.3px;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0), 0 0 1px rgba(13, 13, 13, 0);
}

input[type="range"]::-moz-range-thumb {
  height: 15px;
  width: 15px;
  cursor: pointer;
  background: #ffc600;
  border-radius: 50px;
  box-shadow: 0 0 rgba(0, 0, 0, 0), 0 0 rgba(13, 13, 13, 0);
}

